<template>
  <div>
    <div id="nav" class="nav">
      <router-link to="/" class="brand">{{returnName}}</router-link>
      <button @click="updateFromAndroid('09999999')">Click</button>
      <nav>
        <router-link :to="{name : 'event-list'}">Event List</router-link>|
        <router-link :to="{name : 'event-create'}">Event Create</router-link>
      </nav>
    </div>
  </div>
</template>

<script>
import { getPhonenumber } from "@/assets/js/mobile.js";
export default {
  data() {
    return {
      name: "HELLO"
    };
  },
  methods: {
    updateFromAndroid(name) {
      this.name = name;
      localStorage.setItem("val", this.name);
      getPhonenumber(name);
    }
  },

  computed: {
    // a computed getter
    returnName: function() {
      // `this` points to the vm instance
      return localStorage.getItem("val") == null
        ? localStorage.getItem("val")
        : this.name;
    }
  }
};
</script>

